import React, {lazy, Suspense} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Loading from "../../../components/shared-components/Loading";
import {Redirect, Switch} from "react-router-dom";
import VillaRoute from "../../../permissions/VillaRoute";

const VillaViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'content'}/>}>
            <Switch>
                <VillaRoute path={`${match.url}/profile`} component={lazy(()=> import("./villa-profile/index"))}/>
                <Redirect to={`${match.url}/profile`} from={`${match.url}`}/>
            </Switch>
        </Suspense>
    )
}

export default VillaViews